ay: flex;\r\n  flex-direction: column;\r\n  position: relative;\r\n}\r\n\r\n.chat-panel::before {\r\n  content: '';\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  bottom: 0;\r\n  width: 1px;\r\n  background: linear-gradient(180deg, transparent, rgba(255, 255, 255, 0.05), transparent);\r\n}\r\n\r\n.chat-panel h3 {\r\n  padding: 12px;\r\n  font-size: 11px;\r\n  font-weight: 590;\r\n  color: #98989d;\r\n  border-bottom: 0.5px solid rgba(255, 255, 255, 0.08);\r\n  background: rgba(28, 28, 30, 0.8);\r\n  backdrop-filter: blur(10px);\r\n  -webkit-backdrop-filter: blur(10px);\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.06em;\r\n}\r\n\r\n.chat-messages {\r\n  flex: 1;\r\n  padding: 8px;\r\n  overflow-y: auto;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 6px;\r\n}\r\n\r\n/* Messages with authentic macOS-style bubbles */\r\n.message {\r\n  padding: 6px 10px;\r\n  border-radius: 16px;\r\n  max-width: 80%;\r\n  word-wrap: break-word;\r\n  font-size: 12px;\r\n  line-height: 1.35;\r\n  animation: slide-up 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);\r\n  backdrop-filter: blur(10px);\r\n  -webkit-backdrop-filter: blur(10px);\r\n  position: relative;\r\n}\r\n\r\n.message.system {\r\n  background: rgba(72, 72, 74, 0.6);\r\n  color: #98989d;\r\n  align-self: center;\r\n  text-align: center;\r\n  font-style: italic;\r\n  border: 0.5px solid rgba(72, 72, 74, 0.8);\r\n  font-size: 10px;\r\n}\r\n\r\n.message.user {\r\n  background: rgba(0, 122, 255, 0.8);\r\n  color: #ffffff;\r\n  align-self: flex-end;\r\n  box-shadow: 0 1px 3px rgba(0, 122, 255, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.message.assistant {\r\n  background: rgba(52, 199, 89, 0.8);\r\n  color: #ffffff;\r\n  align-self: flex-start;\r\n  box-shadow: 0 1px 3px rgba(52, 199, 89, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n/* Input with authentic macOS-style focus */\r\n.chat-input {\r\n  padding: 8px;\r\n  border-top: 0.5px solid rgba(255, 255, 255, 0.08);\r\n  display: flex;\r\n  gap: 6px;\r\n  background: rgba(28, 28, 30, 0.8);\r\n  backdrop-filter: blur(20px);\r\n  -webkit-backdrop-filter: blur(20px);\r\n}\r\n\r\n.chat-input input {\r\n  flex: 1;\r\n  padding: 6px 10px;\r\n  background: rgba(72, 72, 74, 0.3);\r\n  border: 0.5px solid rgba(255, 255, 255, 0.1);\r\n  border-radius: 12px;\r\n  color: #ffffff;\r\n  font-size: 12px;\r\n  transition: all 150ms cubic-bezier(0.25, 0.46, 0.45, 0.94);\r\n  font-family: inherit;\r\n}\r\n\r\n.chat-input input:focus {\r\n  outline: none;\r\n  border-color: rgba(0, 122, 255, 0.6);\r\n  box-shadow: 0 0 0 1px rgba(0, 122, 255, 0.3);\r\n  background: rgba(72, 72, 74, 0.5);\r\n}\r\n\r\n.chat-input input:disabled {\r\n  opacity: 0.4;\r\n  cursor: not-allowed;\r\n}\r\n\r\n/* Buttons with authentic macOS-style design */\r\n.chat-input button, .btn-primary, .btn-secondary {\r\n  padding: 6px 12px;\r\n  border: 0.5px solid transparent;\r\n  border-radius: 6px;\r\n  font-weight: 400;\r\n  font-size: 11px;\r\n  cursor: pointer;\r\n  transition: all 150ms cubic-bezier(0.25, 0.46, 0.45, 0.94);\r\n  backdrop-filter: blur(10px);\r\n  -webkit-backdrop-filter: blur(10px);\r\n  font-family: inherit;\r\n  letter-spacing: -0.005em;\r\n}\r\n\r\n.chat-input button, .btn-primary {\r\n  background: rgba(0, 122, 255, 0.8);\r\n  color: #ffffff;\r\n  box-shadow: 0 1px 2px rgba(0, 122, 255, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.2);\r\n  border-color: rgba(0, 122, 255, 0.6);\r\n}\r\n\r\n.chat-input button:hover:not(:disabled), .btn-primary:hover:not(:disabled) {\r\n  background: rgba(0, 122, 255, 0.9);\r\n  transform: translateY(-0.5px);\r\n  box-shadow: 0 2px 6px rgba(0, 122, 255, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.3);\r\n}\r\n\r\n.btn-secondary {\r\n  background: rgba(72, 72, 74, 0.6);\r\n  color: #ffffff;\r\n  border-color: rgba(255, 255, 255, 0.1);\r\n  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.btn-secondary:hover:not(:disabled) {\r\n  background: rgba(72, 72, 74, 0.8);\r\n  